import React, { useState }  from 'react';
import logo from './logo.svg';
import './App.css';
import { green } from '@material-ui/core/colors';
import MaskedInput from 'react-text-mask';
import Autocomplete from '@material-ui/lab/Autocomplete';

import { createMuiTheme } from '@material-ui/core/styles';

import { ThemeProvider  } from '@material-ui/core/styles';

import {
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    TextField,
    OutlinedInput,
    FormControlLabel,
    withStyles,
    Checkbox
} from '@material-ui/core'


import cities from "./data/russia";


const theme = createMuiTheme({
    palette: {
        primary: {
            main: 'rgb(13, 209, 73)'
        },
        secondary: {
            main: '#f44336',
        },
    },
});

const GreenCheckbox = withStyles({
    root: {
        color: 'rgb(13, 209, 73)',
        '&$checked': {
            color: 'rgb(13, 209, 73)',
        },
    },
    checked: {},
})((props) => <Checkbox checked color="default" {...props} />);

function getMaskBaseInput(props) {
    const { inputRef, ...other } = props;

    return (
        <MaskedInput
            {...other}
            ref={(ref) => {
                inputRef(ref ? ref.inputElement : null);
            }}
            label={"Телефон для связи"}

            mask={[ /[1-9]/, ' ', '(', /\d/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/]}
        />
    )
}

function App() {
    const [amountOfCredit, setAmountOfCredit ] = useState(10000);

  return (
      <ThemeProvider theme={theme}>
          <form action="/test" method="GET" >
              <div className="form_section form_section--1" >
                  <div className="form_section__fields form_section__fields--1">
                      <div className="form_control_wrapper">
                          <FormControl variant="outlined" className={"form_item form_control"} >
                              <InputLabel id="form_item--loan_purpose__label">Цель кредита</InputLabel>
                              <Select
                                  defaultValue={1}
                                  required
                                  name="loan_purpose"
                                  labelId="form_item--loan_purpose__label"
                                  id="form_item--loan_purpose__select"
                                  label="Цель кредита"
                              >
                                  <MenuItem value={1}>Ремонт</MenuItem>
                                  <MenuItem value={2}>Покупка нового автомобиля</MenuItem>
                                  <MenuItem value={3}>Покупка поддержанного автомобиля</MenuItem>
                                  <MenuItem value={4}>Погашение кредита</MenuItem>
                                  <MenuItem value={5}>Учеба</MenuItem>
                                  <MenuItem value={6}>Развитие бизнеса</MenuItem>
                                  <MenuItem value={7}>Лечени</MenuItem>
                                  <MenuItem value={8}>Погашение долгов(не кредиты)</MenuItem>
                                  <MenuItem value={9}>Шоппинг</MenuItem>
                                  <MenuItem value={10}>Путешествие</MenuItem>
                                  <MenuItem value={11}>Торжество</MenuItem>
                                  <MenuItem value={12}>Помощь близким</MenuItem>
                                  <MenuItem value={13}>Иное</MenuItem>
                                  <MenuItem value={14}>Пока не определился</MenuItem>
                              </Select>
                          </FormControl>
                      </div>
                      <div className="form_control_wrapper">
                          <FormControl  variant="outlined" className={"form_item form_control"} >
                              <TextField
                                  required
                                  error={amountOfCredit < 10000}
                                  helperText={amountOfCredit < 10000? "Сумма кредита от 10 000 рублей":''}
                                  name="amount_of_credit"
                                  onBlur={e => setAmountOfCredit(e.target.value)}
                                  id="form_item--amount_of_credit__input"
                                  label="Сумма кредита"
                                  type="number"
                                  InputLabelProps={{
                                      shrink: true,
                                  }}
                                  variant="outlined"
                              />
                          </FormControl>
                      </div>
                      <div className="form_control_wrapper">
                          <Autocomplete
                              id="form_item--city_select"
                              className="form_item"
                              // style={{ width: 300 }}
                              options={cities}
                              // classes={{
                              //     option: classes.option,
                              // }}
                              autoHighlight
                              autoSelect
                              noOptionsText={"Ничего не найдено"}
                              defaultValue={
                                  {
                                      "region": "Москва и Московская обл.",
                                      "city": "Москва"
                                  }
                              }
                              getOptionLabel={(option) => option.city}
                              renderOption={(option) => (<>{option.city}</>)}
                              renderInput={(params) => (
                                  <TextField
                                      required
                                      {...params}
                                      label="Город получения"
                                      variant="outlined"
                                      inputProps={{
                                          ...params.inputProps,
                                          autoComplete: 'new-password', // disable autocomplete and autofill
                                      }}
                                  />
                              )}
                          />
                      </div>
                  </div>
              </div>
              <div className="form_section form_section--2" >
                  <h3 className="form_title" >Контактные данные</h3>
                  <div className="form_section__fields form_section__fields--2">
                      <div className="form_control_wrapper">
                          <TextField
                              name="last_name"
                              required
                              id="form_item--last_name"
                              className="form_item"
                              label="Фамилия"
                              variant="outlined"
                          />
                      </div>
                      <div className="form_control_wrapper">
                          <TextField
                              name="name"
                              required
                              id="form_item--name"
                              className="form_item"
                              label="Имя"
                              variant="outlined"
                          />
                      </div>
                      <div className="form_control_wrapper">
                          <TextField
                              name="middle_name"
                              required
                              id="form_item--first_middle_name"
                              className="form_item"
                              label="Отчество"
                              variant="outlined"
                          />
                      </div>
                      <div className="form_control_wrapper">
                          <TextField
                              name="mail"
                              required
                              id="form_item--mail"
                              className="form_item"
                              label="Электронная почта"
                              variant="outlined"
                          />
                      </div>
                      <div className="form_control_wrapper">
                          <FormControl variant="outlined" className={"form_item form_control"} >
                              <InputLabel htmlFor="form_item--telephone_number">Телефон для связи</InputLabel>
                              <OutlinedInput

                                  required
                                  id="form_item--telephone_number"
                                  className="form_item"
                                  label="Телефон для связи"
                                  // labelWidth={200}
                                  inputComponent={getMaskBaseInput}
                              />
                          </FormControl>
                      </div>
                  </div>
                  <FormControlLabel
                      control={<GreenCheckbox name="rules" />}
                      label={
                          <span className="form_checkbox__label" >Согласен с <a className="conditions">правилами предоставления информации</a></span>
                      }
                  />
                  <div className="form__btn_wrapper">
                      <button type="submit" className="section--form__button button button--index">Продолжить</button>
                      <div className="form__btn_description">
                          Если вы уже регистрировались на сайте и заполняли анкету, после подтверждения номера телефона мы автоматически подгрузим ваши персональные данные.
                      </div>
                  </div>

              </div>
          </form>
      </ThemeProvider>
  );
}


export default App;
